/* Overlay with Blur effect */
.cart-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(15, 198, 207, 0.8); /* Dark overlay */
  backdrop-filter: blur(10px); /* Blur effect */
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeIn 0.6s ease-out;
}

/* Cart page with Glowing Border */
.cart-page {
  background: #59046a;
  color: #ffffff;
  padding: 40px;
  width: 90%;
  max-width: 800px;
  max-height: 85vh;
  overflow-y: auto;
  position: relative;
  border-radius: 30px;
  box-shadow: 0 20px 60px rgba(0, 255, 255, 0.5); /* Glowing border */
  transform: translateY(-50px);
  animation: slideUp 0.8s ease-out;
}

/* Close button with Neon Effect */
.close-cart {
  position: absolute;
  top: 20px;
  right: 20px;
  background: transparent;
  border: none;
  color: #ff00ff; /* Neon pink */
  font-size: 30px;
  cursor: pointer;
  text-shadow: 0 0 15px #ff00ff; /* Glowing text */
  transition: color 0.3s, text-shadow 0.3s;
}

.close-cart:hover {
  color: #00ffff; /* Neon cyan */
  text-shadow: 0 0 25px #00ffff; /* Glowing hover effect */
}

/* Cart Items with Floating Effect */
.cart-items {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.cart-item {
  display: flex;
  background: #1a1a1a;
  border-radius: 20px;
  padding: 15px;
  align-items: center;
  box-shadow: 0 10px 25px rgba(0, 255, 255, 0.3);
  transition: transform 0.4s ease, box-shadow 0.4s ease;
  animation: floatUp 1.5s ease-in-out infinite alternate; /* Floating animation */
}

.cart-item:hover {
  transform: translateY(-10px); /* Hover effect */
  box-shadow: 0 15px 35px rgba(0, 255, 255, 0.6);
}

.cart-item img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 10px;
  margin-right: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
}

/* Item Details */
.item-details {
  flex: 1;
  position: relative;
}

.item-details h3 {
  font-size: 24px;
  font-weight: bold;
  color: #00e5ff; /* Neon blue */
  text-shadow: 0 0 15px #00e5ff;
}

.item-details p {
  font-size: 15px;
  color: #d0d0d0; /* Light gray */
}

/* Remove Button with Animated Neon Glow */
.remove-btn {
  position: absolute;
  right: 10px;
  bottom: 10px;
  background: transparent;
  border: none;
  color: #ff00ff; /* Neon pink */
  font-size: 18px;
  cursor: pointer;
  text-shadow: 0 0 15px #ff00ff;
  padding: 10px;
  border-radius: 8px;
  transition: color 0.4s, text-shadow 0.4s, transform 0.4s;
}

.remove-btn:hover {
  color: #ff0000; /* Neon red on hover */
  text-shadow: 0 0 25px #ff0000;
  transform: scale(1.1);
}

/* Cart Summary with Glowing Effects */
.cart-summary {
  text-align: right;
  margin-top: 35px;
  border-top: 1px solid #444;
  padding-top: 20px;
}

.cart-summary .total {
  font-size: 28px;
  font-weight: bold;
  color: #00ff00; /* Neon green */
  text-shadow: 0 0 20px #00ff00; /* Glowing text */
}

.cart-summary .proceed-button {
  background: linear-gradient(145deg, #ff00ff, #00e5ff); /* Neon gradient */
  border: none;
  padding: 18px 30px;
  border-radius: 20px;
  color: white;
  font-size: 20px;
  cursor: pointer;
  text-shadow: 0 0 20px #ff00ff, 0 0 20px #00e5ff; /* Neon glow */
  transition: background 0.3s, transform 0.4s ease, box-shadow 0.4s;
}

.cart-summary .proceed-button:hover {
  background: linear-gradient(145deg, #00e5ff, #ff00ff);
  transform: scale(1.1);
  box-shadow: 0 4px 20px rgba(0, 255, 255, 0.7);
}

/* Empty Cart Animation */
.empty-cart {
  text-align: center;
  padding: 50px;
  background: #ff00ff; /* Neon pink */
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(255, 0, 255, 0.4);
}

.empty-cart h3 {
  font-size: 30px;
  color: #ffffff;
  margin: 0;
  text-shadow: 0 0 30px #ff00ff; /* Glowing effect */
}

.empty-cart p {
  font-size: 20px;
  color: #fff;
  text-shadow: 0 0 20px #ffffff;
}

/* Keyframe Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(50px);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes floatUp {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-10px);
  }
}
