@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(135deg, #07bade, #034249, #c7671e);
  color: #333;
}

/* Hero Section */
.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 100px 5% 60px; /* Enough top padding for nav bar */
  min-height: 100vh;
  gap: 40px;
}

.hero-text {
  flex: 1;
  max-width: 600px;
  text-align: left;
}

.hero-text h1 {
  font-size: 2.5rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 20px;
}

.hero-text p {
  font-size: 1.3rem;
  color: #f0f0f0;
  margin-bottom: 20px;
  line-height: 1.6;
}

/* CTA Button */
.cta-button {
  background-color: #ff6f61;
  color: #fff;
  padding: 14px 36px;
  font-size: 1.1rem;
  font-weight: 600;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  box-shadow: 0 8px 24px rgba(255, 111, 97, 0.4);
  transition: 0.3s ease;
}

.cta-button:hover {
  background-color: #e85c4f;
  transform: scale(1.05);
}

/* Hero Image */
.hero-image {
  flex: 1;
  display: flex;
  justify-content: center;
}

.hero-image img {
  max-width: 800px;
  width: 900px;
  height: 50%;
  border-radius: 30px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
}

/* Modal Styling */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.modal-content {
  position: relative;
  background-color: #fff;
  padding: 40px;
  border-radius: 20px;
  text-align: center;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
}

.modal-content h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  font-weight: 600;
  color: #333;
}

.modal-content p {
  font-size: 1.2rem;
  color: #555;
  line-height: 1.6;
}

/* Modal Close (X) */
.modal-close {
  position: absolute;
  top: 16px;
  right: 20px;
  background: none;
  border: none;
  font-size: 1.8rem;
  color: #999;
  cursor: pointer;
  font-weight: bold;
  transition: color 0.2s ease;
}

.modal-close:hover {
  color: #333;
}

/* Responsive */
@media (max-width: 768px) {
  .hero {
    flex-direction: column-reverse;
    text-align: center;
    padding: 120px 20px 40px; /* More top padding to avoid navbar overlap */
  }

  .hero-text {
    text-align: center;
  }

  .hero-text h1 {
    font-size: 2rem;
  }

  .hero-text p {
    font-size: 1.1rem;
  }

  .hero-image img {
    width: 100%;
    max-width: 100%;
    margin-bottom: 30px;
  }
}
