/* AlternateServicePage.css */

.alt-service-container {
    min-height: 100vh;
    background: linear-gradient(to right, #8aabcc, #e3eaf3);
    padding: 40px;
    text-align: center;
    font-family: 'Arial', sans-serif;
  }
  
  .alt-service-header {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 30px;
    color: #100f0f;
    text-transform: uppercase;
    letter-spacing: 2px;
    background: linear-gradient(90deg, #ff6b6b, #556270);
    padding: 20px;
    border-radius: 10px;
    display: inline-block;
  }
  
  .alt-service-grid {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .alt-service-card {
    background: white;
    width: 100%;
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
    text-align: left;
    overflow: hidden;
    position: relative;
  }
  
  .alt-service-card:hover {
    transform: translateY(-8px);
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  }
  
  .alt-service-image {
    width: 100%;
    height: 300px;
    object-fit: cover;
    border-radius: 10px;
    transition: transform 0.3s ease;
  }
  
  .alt-service-card:hover .alt-service-image {
    transform: scale(1.05);
  }
  
  .alt-service-content {
    padding: 20px;
  }
  
  .alt-service-title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 30px;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 2px;
    background: linear-gradient(90deg, #01254a, #448ddc);
    padding: 20px;
    border-radius: 10px;
    display: inline-block;
  }
  
  .alt-service-description {
    color: #555;
    font-size: 1.2rem;
    line-height: 1.8;
  }
  
  .alt-service-footer {
    margin-top: 50px;
    font-size: 1.2rem;
    color: #333;
  }
  
  .alt-contact-link {
    color: #2980b9;
    text-decoration: none;
    font-weight: bold;
    transition: color 0.3s;
  }
  
  .alt-contact-link:hover {
    color: #1f618d;
    text-decoration: underline;
  }
  