/* Background and layout */
.product-grid {
    background: #5788a8;
    padding: 50px 20px;
    min-height: 100vh;
    font-family: 'Poppins', sans-serif;
  }
  
  .product-grid-title {
    text-align: center;
    font-size: 32px;
    color: #333;
    margin-bottom: 40px;
  }
  
  /* Grid for product cards */
  .product-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 30px;
    justify-items: center;
  }
  
  /* Each product card */
  .product-card {
    background-color: #f0f7f8;
    border-radius: 20px;
    padding: 20px;
    width: 100%;
    max-width: 320px;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .product-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 10px 24px rgba(0, 0, 0, 0.15);
  }
  
  .product-image {
    width: 100%;
    height: 180px;
    object-fit: cover;
    border-radius: 12px;
    margin-bottom: 15px;
  }
  
  .product-title {
    font-size: 20px;
    color: #222;
    margin: 10px 0;
  }
  
  .product-price {
    font-size: 18px;
    color: #007bff;
    font-weight: 600;
    margin-bottom: 15px;
  }
  
  .product-buttons {
    display: flex;
    justify-content: space-between;
    gap: 10px;
  }
  
  .cart-btn,
  .buy-btn {
    flex: 1;
    padding: 10px;
    font-size: 15px;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .cart-btn {
    background-color: #343a40;
    color: #fff;
  }
  
  .cart-btn:hover {
    background-color: #23272b;
  }
  
  .buy-btn {
    background-color: #ff6f61;
    color: #fff;
  }
  
  .buy-btn:hover {
    background-color: #e55a4e;
  }
  